@use './variables' as *;



@mixin tablet {
    @media only screen and (max-width:$tablet) {
        @content;
    }
}

@mixin mobile {
    @media only screen and (max-width:$mobile) {
        @content;
    }
}

@mixin mobSmall {
    @media only screen and (max-width:$mobileSmall) {
        @content;
    }
}